<template>
    <div class="container">
      <h1>运势测算</h1>
      <form @submit.prevent="submitForm">
        <div class="form-group">
          <label>出生日期：</label>
          <div class="date-picker">
            <select v-model="year" required>
              <option v-for="y in years" :key="y" :value="y">{{ y }}年</option>
            </select>
            <select v-model="month" required>
              <option v-for="m in 12" :key="m" :value="m">{{ m }}月</option>
            </select>
            <select v-model="day" required>
              <option v-for="d in 31" :key="d" :value="d">{{ d }}日</option>
            </select>
          </div>
        </div>
        
        <div class="form-group">
          <label>性别：</label>
          <label><input type="radio" v-model="gender" value="male" required> 男</label>
          <label><input type="radio" v-model="gender" value="female"> 女</label>
        </div>
  
        <button type="submit">生成运势</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        year: new Date().getFullYear(),
        month: 1,
        day: 1,
        gender: 'male',
        years: Array.from({length: 100}, (_, i) => new Date().getFullYear() - i - 18)
      }
    },
    methods: {
      submitForm() {
        const birthDate = `${this.year}-${this.month}-${this.day}`
        this.$router.push({
          path: '/report',
          query: { birthDate, gender: this.gender }
        })
      }
    }
  }
  </script>
  
  <style scoped>
  .container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
  }
  .date-picker select {
    margin-right: 10px;
    padding: 5px;
  }
  .form-group {
    margin: 20px 0;
  }
  button {
    background: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  </style>